// -----------------------------------------------------------------------------
// Button component
// -----------------------------------------------------------------------------
.btn-outline-secondary:not(:disabled):not(.disabled):active, .btn-outline-secondary:not(:disabled):not(.disabled).active, .show > .btn-outline-secondary.dropdown-toggle{
    background-color:$white;
    color:inherit;
    box-shadow: none !important;
    border: none;
}

// .btn:hover, .btn-outline-secondary:hover{
//     color:inherit;
//     box-shadow: none !important;
//     border: none;
// }
button:focus, button:active{   
    outline: none;
}

.btn{
    height:40px;
    font-size: 1rem;
    font-weight:700;
    box-shadow: none;
    border-radius: 0;
    width:100%;

    &.md-fix{
        min-width:92px;
        width: 92px;
        padding:0;
    }

    &.md-fix-height{
        min-width:92px;
        width: 92px;
        height:32px;
        padding:0;
    }

    &.lg-fix{
        min-width:160px;
        width: 160px;
        padding:0;
        @include media-breakpoint-down(xs){
            min-width:90px;
            width: 90px;
        }
    }

    &.lg-fix-height{
        min-width:160px;
        width: 160px;
        height:40px;
        padding:0;
        @include media-breakpoint-down(xs){
            min-width:90px;
            width: 90px;
        }
    }

    &-primary{
        background-color:$primary;
        color:$white;
        border:none;
    }


    &:focus{     
        outline:none;
        box-shadow:none;
    }

    &-outline{
        border:1px solid #e9ecef;
        background-color: $white;

        &-primary{
            background-color: $white;
            border:1px solid $primary;            
        }

         &-secondary{
            background-color: $white;
            border:1px solid $light;
            color:$main-grey; 
        }
    }

}

//filter btn
button.btn.filter-btn:hover{
    background-color: $white;
    color:$main-grey;
}

.not-focus{    
    &:not(:disabled):not(.disabled){
        border: none;
        outline: none;
    }
}

//button list
.buttons{
    display: flex;
    flex-wrap: wrap;
    button{
        width:calc(100% / 2 - 8px);
        margin: 0;

        &.list-one{
            margin-right: 4px;
        }

        &.list-two{
            margin-left:4px;
        }
    }
}